import { useState } from "react";
import classes from "./../../users/users.module.css"

const Paginator = ({ totalItemsCount, pageSize, changePage, currentPage, portionSize = 10 }) => {
  let pageCount = Math.ceil(totalItemsCount / pageSize); //      (pageSize      5   1 ).
  let pages = []; //    
  for (let i = 1; i <= pageCount; i++) {
    pages.push(i);
  }

  let portionCount = Math.ceil(pageCount / portionSize); //    10  
  let [portionNumber, setPortionNumber] = useState(1); // useState    2 -: 1 -   ; 2 - callback ,     .     .
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1; //     (  :    1       (5)   1).
  let rightPortionPageNumber = portionNumber * portionSize; //    

  return (
    <div>
      {portionNumber > 1 && //   2  ,    ,     .
        <button className={classes.PaginatorBtn} onClick={() => {
					setPortionNumber(portionNumber - 1)
			}}>Prev</button>
      }

      {pages
      .filter(el => leftPortionPageNumber <= el && el <= rightPortionPageNumber) //    ,            .
      .map((el) => {
        return (
          <span
            onClick={() => {
              changePage(el);
            }}
            key={el}
            className={currentPage === el ? classes.selectedPage : classes.page}
          >{`${el} `}</span>
        );
      })}

      {portionCount > portionNumber && //    ,    ,    . .
        <button className={classes.PaginatorBtn} onClick={() => {
					setPortionNumber(portionNumber + 1)
			}}>Next</button>
      }

    </div>
  );
};

export default Paginator;
